// Checkbox.tsx
import clsx from 'clsx';
import { forwardRef } from 'react';
import { FunctionalIcon } from '../icon/Icon';
import { focusRing } from './../../tailwind/styles/focus';
import { CheckboxProps } from './checkbox.interface';

export const Checkbox: React.FC<CheckboxProps> = forwardRef<HTMLInputElement, CheckboxProps>(
  ({ id, name, disabled = false, error = false, label, children, onChange, value = id, ...props }, ref) => {
    return (
      <span
        className={clsx(
          'zep-group',
          'zep-relative',
          'zep-inline-flex',
          'zep-flex-row',
          'zep-flex-wrap',
          'zep-items-center',
          'zep-justify-start',
          {
            '[&>svg]:hover:zep-bg-neutral-dark-hover': !disabled && !error,
          },
          { 'zep-cursor-not-allowed': disabled },
        )}
        data-testid="zep-checkbox"
      >
        <input
          id={id}
          ref={ref}
          name={name}
          value={value}
          type="checkbox"
          onChange={!disabled ? onChange : undefined}
          className={clsx(
            focusRing,
            'sr-only',
            'zep-peer',
            'zep-absolute',
            'zep-top-[0]',
            'zep-rounded-4',
            'zep-appearance-none',
            'focus-visible:zep-ring-offset-1',
            'zep-w-1.5',
            'zep-h-1.5',
            'zep-rounded-4',
            'zep-border-1',
            'checked:zep-border-none checked:zep-bg-neutral-dark-default',
            { 'checked:zep-bg-opacity-disabled': disabled },
            { 'zep-border-2 zep-border-error': error },
            { 'zep-border-neutral-dark-default group-hover:zep-border-neutral-dark-hover': !error && !disabled },
            { 'zep-border-neutral-dark-default zep-border-opacity-disabled': disabled },
          )}
          disabled={disabled}
          data-testid="zep-checkbox-input"
          {...props}
        />
        <label
          htmlFor={id}
          className={clsx(
            'zep-body-text',
            'zep-relative',
            'zep-z-20',
            'zep-pl-[36px]',
            'zep-text-typography-dark-100',
            [disabled ? 'zep-cursor-not-allowed zep-text-opacity-disabled' : 'zep-cursor-pointer'],
          )}
        >
          {children || label}
        </label>
        <FunctionalIcon
          name="check"
          className={clsx(
            'zep-hidden peer-checked:zep-block',
            'zep-text-neutral-dark-contrast',
            'zep-rounded-4',
            'zep-w-1.5',
            'zep-h-1.5',
            'zep-z-10',
            'zep-absolute',
            'zep-top-[-0.1px]',
            'zep-left-[-0.1px]',
            { 'zep-bg-transparent': !error && !disabled },
            { 'zep-border-opacity-disabled zep-bg-opacity-disabled': disabled },
            { 'zep-bg-error': error },
          )}
        />
      </span>
    );
  },
);
